.my-container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

/* xs */
@media (min-width: 475px) {
  .my-container {
    max-width: 475px;
  }
}

/* sm */
@media (min-width: 640px) {
  .my-container {
    max-width: 640px;
  }
}

/* md */
@media (min-width: 768px) {
  .my-container {
    max-width: 768px;
  }
}

/* lg */
@media (min-width: 1024px) {
  .my-container {
    max-width: 1024px;
  }

  .section {
    margin-top: 10rem;
  }
}

/* xl */
@media (min-width: 1280px) {
  .my-container {
    max-width: 1280px;
  }
}

/* 2xl */
@media (min-width: 1536px) {
  .my-container {
    max-width: 1536px;
  }
}
